import { expose } from 'comlink';
import { SORT_TYPES, SEARCHABLE_PROPERTIES_BY_DATA_TYPE } from './constants';

/**
 * @type {Map<string, Promise<{{ [key: string]: object }}>}
 */
const updateStatsByServerDataTypePair = new Map();
const DEFAULT_PAGE_SIZE = 50;
let searchContext = {
	searchKey: '',
	results: [],
};

function fetchJson (url, options) {
	return fetch(url, options)
		.then((response) => response.ok
			? response.json()
			: Promise.reject({ status: response.status, statusText: response.statusText })
		);
}

function setSearchContext (results = []) {
	searchContext = {
		searchKey: `${Math.random()}-${Date.now()}`,
		results,
	};
}

function createServerDataTypeKey (server, dataType) {
	return `${server}-${dataType}`;
}

function getUpdateStatsForServerAndDataType (server, dataType, { sortType = 'ID', sortMultiplier = 1, maxResultSize = DEFAULT_PAGE_SIZE } = {}) {
	const key = createServerDataTypeKey(server, dataType);
	let result = updateStatsByServerDataTypePair.get(key);
	if (!result) {
		result = fetchJson(`https://raw.githubusercontent.com/BluuArc/bfmt-data/master/${server}/${dataType}/_update-stats.json`);
		updateStatsByServerDataTypePair.set(key, result);
	}
	return result.then((r) => {
		const sortFunction = SORT_TYPES[sortType] || SORT_TYPES.ID;
		const entries = Object.entries(r)
			.map(([key, value]) => ({ key, value }))
			.sort((...args) => sortMultiplier * sortFunction(...args));
		setSearchContext(entries);
		return {
			searchKey: searchContext.searchKey,
			totalEntries: entries.length,
			results: entries.slice(0, maxResultSize),
		};
	});
}

async function getEntriesForSearch (server, dataType, { filters = {}, sortType = 'ID', sortMultiplier = 1, maxResultSize = DEFAULT_PAGE_SIZE } = {}) {
	await getUpdateStatsForServerAndDataType(server, dataType); // ensure data exists

	const key = createServerDataTypeKey(server, dataType);
	const fullDb = await updateStatsByServerDataTypePair.get(key);

	let filteredResults = [];
	const allEntries = Object.entries(fullDb);
	if (!filters.text) {
		filteredResults = allEntries;
	} else {
		const availableProperties = SEARCHABLE_PROPERTIES_BY_DATA_TYPE[dataType] || [];
		const normalizedText = `${filters.text}`.toLowerCase().trim();
		if (normalizedText) {
			const includesText = (val) => !!val && `${val}`.toLowerCase().includes(normalizedText);
			filteredResults = allEntries.filter(
				([key, value]) =>
					includesText(key) ||
					(value && availableProperties.some((p) => includesText(value[p]))
				)
			);
		}
	}

	const sortFunction = SORT_TYPES[sortType] || SORT_TYPES.ID;
	filteredResults = filteredResults
		.map(([key, value]) => ({ key, value }))
		.sort((...args) => sortMultiplier * sortFunction(...args));

	setSearchContext(filteredResults);
	return {
		searchKey: searchContext.searchKey,
		totalEntries: filteredResults.length,
		results: filteredResults.slice(0, maxResultSize),
	};
}

function updateSearchContextSort (searchKey, { sortType = 'ID', sortMultiplier = 1, maxResultSize = DEFAULT_PAGE_SIZE } = {}) {
	if (!searchContext || !searchContext.searchKey) {
		throw new Error('Search not initialized');
	}
	if (searchContext.searchKey !== searchKey) {
		throw new Error('Search key mismatch');
	}

	const sortFunction = SORT_TYPES[sortType] || SORT_TYPES.ID;
	searchContext.results = searchContext.results.sort((...args) => sortMultiplier * sortFunction(...args));

	return {
		searchKey: searchContext.searchKey,
		totalEntries: searchContext.results.length,
		results: searchContext.results.slice(0, maxResultSize),
	};
}

function getEntriesInSearchContext (searchKey, startIndex, maxResultSize = DEFAULT_PAGE_SIZE) {
	if (!searchContext || !searchContext.searchKey) {
		throw new Error('Search not initialized');
	}
	if (searchContext.searchKey !== searchKey) {
		throw new Error('Search key mismatch');
	}

	return searchContext.results.slice(startIndex, startIndex + maxResultSize);
}

function getEntry (server, dataType, key) {
	return fetchJson(`https://joshuacastor.me/bfmt-data/${server}/${dataType}/${key}.json`)
}

expose({
	getUpdateStatsForServerAndDataType,
	getEntriesForSearch,
	updateSearchContextSort,
	getEntriesInSearchContext,
	getEntry,
});
